@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Gloock&family=Poppins:ital,wght@0,200;1,100;1,200&family=Qwitcher+Grypen:wght@700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  @apply font-[Raleway];
}
html,
body {
  height: 100%;
}
li {
  cursor: pointer;
  @apply px-4;
}

.first {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 250px;
}

.first:hover {
  background-image: linear-gradient(to right, #1ef9fa, #1e85fa) !important;
}
